<template>
  <div id="common-box">
      <div class="edit-form">
        <div class="edit-inner">
            <h2>
              <p>修改密码</p>
              <p class="f14">FIND</p>
            </h2>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
              <el-form-item label="旧密码" prop="oldPass">
                <el-input type="password" v-model="ruleForm.oldPass" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="新密码" prop="newPass">
                <el-input type="password" v-model="ruleForm.newPass" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="确认新密码" prop="repeatPass">
                <el-input type="password" v-model="ruleForm.repeatPass" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">确认</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
              </el-form-item>
            </el-form>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  data(){
    const vaildateSame=(rule,value,callback)=>{
      if(this.ruleForm.newPass !== value){
        callback('两次输入密码不同');
      }else{
        callback();
      }
    };
    return{
      ruleForm:{
        oldPass:'',
        newPass:'',
        repeatPass:''
      },
      rules:{
        oldPass:[
          { required:true,message:'请输入旧密码',trigger:'blur'},
          { min:6,max:12,message:'密码长度为6至16',trigger:'blur'}
        ],
        newPass:[
          { required:true,message:'请输入新密码',trigger:'blur'},
          { min:6,max:12,message:'密码长度为6至16',trigger:'blur'}
        ],
        repeatPass:[
          { required:true,message:'请再次输入新密码',trigger:'blur'},
          { min:6,max:12,message:'密码长度为6至16',trigger:'blur'},
          { validator:vaildateSame,trigger:'blur'}
        ]
      }
    }
  },
  methods:{
    submitForm(formVal){
      let that=this;
      this.$refs[formVal].validate((valid)=>{
          if(valid){
            this.$api.editpass(
              that.ruleForm.oldPass,
              that.ruleForm.newPass).then(res=>{
                if(res.err_code === 100){
                  that.$message('密码修改成功');
                  that.$refs[formVal].resetFields();
                }else if(res.err_code === 101){
                  that.$message('原密码错误');
                  that.$refs[formVal].resetFields();
                }
              })
          }
      })
    },
    resetForm(formName){
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss" scoped>
    
</style>